<script setup lang="ts">
import { computed, onMounted, ref } from 'vue'
import { storeToRefs } from 'pinia'
import RightDrawer from '@/shared/ui/layout/RightDrawer.vue'
import { useUser } from '@/shared/api/useUser'
import { useUi } from '@/shared/lib/composables/useUi'
import { useIncident } from '@/shared/api/useIncident'
import IconClose from '@/shared/ui/icons/IconClose.vue'
import ProgressSpinner from '@/shared/ui/ProgressSpinner.vue'
import { useHelpers } from '@/shared/lib/utils/useHelpers'
import ActionSuccessAlert from '@/shared/ui/forms/ActionSuccessAlert.vue'
import ActionErrorAlert from '@/shared/ui/forms/ActionErrorAlert.vue'
import { useUserStore } from '@/shared/api/stores/user'

import {
  ClientFitness,
  EnvironmentalLevel,
  Gender,
  GroupDirection,
  IncidentLevel,
  Location,
  clientAges,
  groupSizeMax,
  incidentDepartureNames,
  incidentLocations,
  incidentRootCauses,
} from '@/shared/config'

const { validateForm, actionIsInProgress, actionIsInError, actionIsInSuccess } = useUi()
const { getCurrentDateInYmdFormat } = useHelpers()
const createIncidentFormElement = ref<HTMLFormElement | null>(null)
const { createIncident, incidentActions } = useIncident()
const { fetchAndStoreUsersIndex } = useUser()
const { storeUsers } = storeToRefs(useUserStore())

// get the current time in the format H:m:s
const getCurrentTimeInHmFormat = () => {
  const date = new Date()
  const hours = date.getHours()
  const minutes = date.getMinutes()
  return `${hours}:${minutes}`
}

const formattedDate = `${getCurrentDateInYmdFormat()}T${getCurrentTimeInHmFormat()}`
const maxDateTimeForIncident = `${getCurrentDateInYmdFormat()}T23:59`
const createIncidentFormData = ref({
  name_of_departure: 'Glacier Wonders',
  incident_master_location: Location.Skaftafell,
  incident_location: 'Kings Landing',
  date_of_departure: formattedDate,
  incident_level: IncidentLevel.NearMiss,
  group_size: 1,
  staff_member_involved_id: 0,
  group_ascending_or_descending: GroupDirection.Asc,
  client_name: '',
  client_age: '25-29',
  client_gender: Gender.Male,
  client_ability: ClientFitness.Medium,
  client_email: '',
  rain: EnvironmentalLevel.Low,
  wind: EnvironmentalLevel.Low,
  temp: EnvironmentalLevel.Low,
  ice: EnvironmentalLevel.Low,
  forecast: EnvironmentalLevel.Low,
  incident_description: '',
  rope_rescue_details: '',
  first_aid_details: '',
  evacuation_details: '',
  root_cause: incidentRootCauses[0],
})
const incident_locations = ref(incidentLocations)
const filteredIncidentLocations = computed(() => {
  return incident_locations.value.find(
    (il) => il.masterLocation === createIncidentFormData.value.incident_master_location,
  )!.locations
})

onMounted(async () => {
  await fetchAndStoreUsersIndex(
    1,
    false,
  )
  createIncidentFormData.value.staff_member_involved_id = storeUsers.value[0].id
})

const changeMasterLocation = (e: Event) => {
  if (e.target instanceof HTMLSelectElement) {
    createIncidentFormData.value.incident_master_location = e.target.value

    createIncidentFormData.value.incident_location = incident_locations.value.find(
      (l) => l.masterLocation === createIncidentFormData.value.incident_master_location,
    )!.locations[0]
  }
}
</script>

