                v-model="createIncidentFormData.client_email"
                type="email"
                class="block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
              >
            </div>
          </div>
        </div>

        <div class="grid grid-cols-1 gap-x-4 gap-y-6 pt-6 sm:grid-cols-2 md:grid-cols-3 xl:grid-cols-5">
          <div class="sm:col-span-1">
            <label for="environmental_rain" class="block text-sm font-medium text-gray-700">
