            <div class="mt-1">
              <select
                id="environmental_temp"
                v-model="createIncidentFormData.temp"
                class="block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
              >
                <option v-for="lvl in EnvironmentalLevel" :key="lvl">
                  {{ lvl }}
                </option>
              </select>
            </div>
          </div>
          <div class="sm:col-span-1">
            <label for="environmental_ice" class="block text-sm font-medium text-gray-700">
